import TellerService from '@/services/bank/TellerService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let TellerTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultTellerTableFilter: {
      type: String,
      default: null
    },
    defaultTellerTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      tellerTableData: [],
      tellerTableFilterTimer: null,
      tellerTableFilter: this.defaultTellerTableFilter,
      tellerTableOrderBy: this.defaultTellerTableOrderBy,
      tellerTableContainer: 'loading-container',
      tellerTableBusy: false,
      tellerTableTotalRows: 0,
      tellerTableCurrentPage: 1,
      tellerTablePerPage: 10
    }
  },
  computed: {
    tellerTableNoDataText: function () {
      return this.tellerTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    tellerTableTotalPages: function () {
      return Math.ceil(this.tellerTableTotalRows / this.tellerTablePerPage)
    },
    tellerTablePagingInfo: function () {
      return this.pagingInfo(this.tellerTableCurrentPage, this.tellerTablePerPage, this.tellerTableTotalRows)
    }
  },
  methods: {
    loadTellerTableData (loadingContainerId) {
      let self = this
      this.tellerTableBusy = true
      this.tellerTableContainer = loadingContainerId || this.tellerTableContainer
      this.startLoading(this.tellerTableContainer)
      TellerService.tellers({
        pageIndex: this.tellerTableCurrentPage - 1,
        pageSize: this.tellerTablePerPage,
        filter: this.tellerTableFilter,
        orderByField: this.tellerTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.tellerTableContainer)
        self.tellerTableBusy = false
        self.tellerTableTotalRows = response.data.totalRows
        self.tellerTableData = response.data.tellerList || []
      }).catch(function (error) {
        self.stopLoading(self.tellerTableContainer)
        self.tellerTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshTellerTable () {
      this.loadTellerTableData()
    },
    filterTellerTable (filter) {
      let self = this
      if (this.tellerTableFilter !== filter) {
        clearTimeout(this.tellerTableFilterTimer)
        this.tellerTableFilterTimer = setTimeout(() => {
          self.tellerTableFilter = filter
          self.loadTellerTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.tellerTableFilterTimer)
      }
    },
    orderTellerTable (orderBy, active) {
      this.tellerTableOrderBy = (active ? '' : '-') + orderBy
      this.loadTellerTableData()
    }
  },
  watch: {
    tellerTableCurrentPage: function () {
      this.refreshTellerTable()
    },
    tellerTablePerPage: function () {
      this.refreshTellerTable()
    }
  }
}

export default TellerTableMixin
